<template>
  <div id="internal-renderer"></div>
</template>

<script>
import * as THREE from 'three';

//import Stats from 'three/examples/jsm/libs/stats.module.js';

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
//import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';

const EARTH_SCALE = .21236667

var init = (ctx) => {
  var scene, controls;

  var params = {
    exposure: 1,
    bloomStrength: 1.5,
    bloomThreshold: .5,
    bloomRadius: 0
  };

  var container = document.getElementById( 'internal-renderer' );

  ctx.rendererWidth = container.clientWidth;
  ctx.rendererheight = container.clientHeight;


  //stats = new Stats();
  //container.appendChild( stats.dom );

  ctx.renderer = new THREE.WebGLRenderer( { antialias: true } );
  ctx.renderer.setPixelRatio( window.devicePixelRatio );
  ctx.renderer.setSize( ctx.rendererWidth, ctx.rendererHeight );
  ctx.renderer.toneMapping = THREE.ReinhardToneMapping;
  container.appendChild( ctx.renderer.domElement );

  scene = new THREE.Scene();
  scene.background = new THREE.Color( 0x000 );

  ctx.camera = new THREE.PerspectiveCamera( 45, ctx.rendererWidth / ctx.rendererHeight, .001, 100 );
  ctx.camera.position.set( - 3, 2.5, - 3.5 );
  scene.add( ctx.camera );

  controls = new OrbitControls( ctx.camera, ctx.renderer.domElement );
  controls.maxPolarAngle = Math.PI * 1.;
  controls.minDistance = 2;
  controls.maxDistance = 18;

  scene.add( new THREE.AmbientLight( 0x1e2324 ) );


  var renderScene = new RenderPass( scene, ctx.camera );

  ctx.bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
  ctx.bloomPass.threshold = 0.0125;
  ctx.bloomPass.strength = params.bloomStrength;
  ctx.bloomPass.radius = 0;

  ctx.composer = new EffectComposer( ctx.renderer );
  ctx.composer.addPass( renderScene );
  ctx.composer.addPass( ctx.bloomPass );

  var materials = [];
  materials['green'] = new THREE.MeshBasicMaterial( { color: 0x00ff00, wireframe: true, wireframeLinewidth: 3 } );
  materials['red'] = new THREE.MeshBasicMaterial( { color: 0x56C1FF, wireframe: true, wireframeLinewidth: 3 } );

  var earthGeometry = new THREE.SphereGeometry(EARTH_SCALE, 10, 10);

  var earth = new THREE.Mesh( earthGeometry, materials['green'] );
  scene.add( earth );

  var radius = EARTH_SCALE * 2.3;
  var radials = 6;
  var circles = 4;
  var divisions = 64;

  var equator = new THREE.PolarGridHelper( radius, radials, circles, divisions );
  scene.add( equator );
  //var gridHelper = new THREE.GridHelper( EARTH_SCALE * 3, 10, 0x0000ff );
  //scene.add( gridHelper );


  ctx.orbitMesh = new THREE.PolarGridHelper( 1, -1, 1, 64, 0x56C1FF, 0x56C1FF);
  //ctx.orbitMesh.matrix = ctx.orbitalMatrix;
  //ctx.orbitMesh.matrixAutoUpdate = false;
  scene.add( ctx.orbitMesh );


  animate();

  //setInterval(500, animate);

  /*var resizeScene = () => {

    var width = ctx.rendererWidth;
    var height = ctx.rendererHeight;

    ctx.camera.aspect = width / height;
    ctx.camera.updateProjectionMatrix();

    ctx.renderer.setSize( width, height );
    ctx.composer.setSize( width, height );

  };*/


  function animate () {
    requestAnimationFrame( animate );

    computeOrbitalMatrix(ctx);
    ctx.orbitMesh.matrix = ctx.orbitalMatrix;
    ctx.orbitMesh.matrixAutoUpdate = false;
    earth.rotation.y -= 0.005;

    ctx.bloomPass.radius = .25 - (Math.pow( Math.sin( Date.now() / 1000), 4.0 ));
    ctx.bloomPass.strength = 1.5 + (Math.pow( Math.sin( Date.now() / 1000 ), 4.0 ));
    //console.log(ctx.renderer.toneMappingExposure);
    //stats.update();
    ctx.composer.render();
  }

  return ctx.renderer, ctx.composer, ctx.camera;
}

var resizeScene = (ctx) => {
  var width = ctx.rendererWidth;
  var height = ctx.rendererHeight;

  ctx.camera.aspect = width / height;
  ctx.camera.updateProjectionMatrix();

  ctx.renderer.setSize( width, height );
  ctx.composer.setSize( width, height );

  //computeOrbitalMatrix(ctx);
}

var computeOrbitalMatrix = (ctx) => {
  const PHYSICS_GM = 3.9857605760e5;
  const SCALE = 6000;
  //const EARTH_TILT = 0.4090470572;

  let radius = new THREE.Matrix4()/*
    , earth_tilt = new THREE.Matrix4()*/
    , periapsis = new THREE.Matrix4()
    , right_ascending_node = new THREE.Matrix4()
    , inclination = new THREE.Matrix4()
    , translate= new THREE.Matrix4()
    , aux = new THREE.Matrix4();

  //let eccentricity = 1.0 - 0.72; //- t->eccentricity

  if (ctx.orbitalMatrix == null)
    ctx.orbitalMatrix = new THREE.Matrix4();

  ctx.orbitalMatrix.identity();
  radius.identity();
  translate.identity();
  aux.identity();

  let b = 1./Math.sqrt(Math.pow(ctx.eccentricity, 2) + 1.0); //0.0006613
  let a = 1./b;

  let period = 86400./ctx.meanMotion; //temp.mean_motion; 15.37715795

  let tleRadius = Math.pow(
        ((period*period) * PHYSICS_GM)/(4.0 * (Math.PI * Math.PI)),
        (1.0/3.0)
    ) / SCALE; //RA 328.3705   //INCL 45.017     //AP 330.017



  let foci = Math.sqrt(Math.pow(tleRadius*a, 2) - Math.pow(tleRadius*b, 2)); //// TODO: Normalize this shit

  //console.log(tleRadius, foci, b, a);

  radius.makeScale(tleRadius * b, 1, tleRadius * (a));
  aux.premultiply(radius);

  translate.makeTranslation(0., 0., foci);
  aux.premultiply(translate);

  right_ascending_node.makeRotationY(-ctx.rightAscension - Math.PI/2.);
  aux.premultiply(right_ascending_node);

  inclination.makeRotationX(-ctx.inclination);
  aux.premultiply(inclination);

  periapsis.makeRotationY(ctx.perigee);
  aux.premultiply(periapsis);

  //radius.makeRotationZ(EARTH_TILT/2.);
  //radius.makeRotationY(-328.3705 - Math.PI/2.);
  //radius.makeRotationX(-45.017);
  //radius.makeRotationY(-330.017);
  /*mat4x4_rotate_Z(earth_tilt, m, EARTH_TILT/2.f);
  	mat4x4_rotate_Y(right_ascending_node, earth_tilt, -t->right_ascension - 3.14159265f/2.f);
  	mat4x4_rotate_X(inclination, right_ascending_node, -t->inclination);
  	mat4x4_rotate_Y(periapsis, inclination, -t->argument_of_periapsis);
  	mat4x4_mul(aux, periapsis, translate);	*/
    //https://earthobservatory.nasa.gov/features/OrbitsCatalog
  ctx.orbitalMatrix.premultiply(aux);
}



