/*for(let i=0; i<64; i++){
    let a = i/64.0;

    a *= Math.PI*2.0;

    ctx.verts[i*3 + 0] = Math.cos(a);
    ctx.verts[i*3 + 1] = 0.0;
    ctx.verts[i*3 + 2] = Math.sin(a);
  }
}*/

export default {
  name: 'OrbitRenderer',
  props : [
    'tleStr',
    'satelliteName',
    'catalogNumber',
    'cospar',
    'classification',
    'designatorYear',
    'designatorLaunchNumber',
    'designatorPieceOfLaunch',
    'epochYear',
    'epochDay',
    'epochTimestamp',
    'revNumberAtEpoch',
    'firstTimeDerivative',
    'bstarDrag',
    'tleSetNumber',
    'isValidTLE',
    'inclination',
    'rightAscension',
    'eccentricity',
    'perigee',
    'meanMotion',
    'meanAnomaly'
  ],
  data () {
    return {
      verts: [],
      orbitalMatrix: null,
      renderer: null,
      composer: null,
      camera: null,
      bloomPass: null,
      orbitMesh: null,
      rendererWidth: 200,
      rendererHeight: 500,
    }
  },
  mounted () {
    computeOrbitalMatrix(this);
    init(this);
  },
  watch: {
    rendererWidth () {
      resizeScene(this);
    },
    rendererHeight () {
      resizeScene(this);
    }
  }
}
</script>
